.header {
  position: fixed;
  display: flex;
  top: 0;
  left: 0;
  height: 40px;
  width: 100%;
  background: linear-gradient(10deg, #03df50, #1a9bd4);
  background-color: #0d9cfd;
  box-shadow: 0 1px 10px rgba(13, 156, 253, 0.8);
  backdrop-filter: blur(10px);
  z-index: 1111;
  transition: all 1s ease-in-out;
}

nav {
  display: flex;
  align-items: center;
  transition: transform 1s ease;
}

nav.gay {
  transform: translateX(-999%);
}

.header .logo {
  position: fixed;
  top: 1px;
  left: 0;
  margin: 0.2rem 40px;
  width: 6rem;
  transition: all 1s ease-in-out;
}

.header.gay {
  height: 100%;
}

.header .logo.gay {
  top: 50%;
  left: 50%;
  transform: translate(-80%, 0) scale(5.5);
  z-index: 69;
  opacity: 1;
  transition: all 1s ease-in-out;
}

.header .logo.gay.ini {
  opacity: 0;
}

nav a {
  padding: 0px 15px;
  text-decoration: none;
  color: #fff;
  margin: 0 10px;
  font-size: 20px;
  font-family: mojangles;
  transition: all 1s ease;
}

nav a:hover {
  font-weight: bold;
  color: #b7ff00;
  border-bottom: 3px solid #51ff00;
  transition: all 0.5s ease;
}

nav a.blyat {
  font-weight: bold;
  font-size: 22px;
  color: #007bff;
  border-bottom: 4px solid #007bff;
}