
.content2 {
  color: #000;
  background: #fff;
  border-radius: 10px;
  box-shadow: 0px 0px 20px 10px rgba(0, 0, 0, 0.26);
  margin: 10vw;
  /* margin-top: 20vh; */
  display: flex;
  padding: 3vw;
  margin-bottom: 0px;
  flex-direction: column;
  align-items: center;
}

.panel-bg {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 69-1;
  width: 100vw;
  height: 100vh;
  background-color: #000;
}

.panel-bg.fade {
  opacity: 0;
}

.panel-bg.show {
  opacity: .5;
}

.panel {
  position: fixed;
  display: none;
  top: 0;
  left: 0;
  color: #000;
  width: 100%;
  height: 100%;
  z-index: 69;
}

.fade {
  transition: opacity .5s linear;
}

.panel .panel-con.fade {
  transition: transform .5s ease-out;
  transform: translate(0, 696px);
}

.panel.show .panel-con {
  transform: none;
}

.panel.show {
  display: flex;
  align-items: center;
}

.panel-con {
  position: relative;
  display: block;
  width: 100%;
  max-width: 500px;
  margin-right: auto;
  margin-left: auto;
  color: #000;
  background-color: #fff;
  border: 1px solid #000;
  border-radius: .5rem;
}

.panel-hea {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem;
  border-bottom: 1px solid #dee2e6;
}

.panel-tit {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 1.25rem;
  line-height: 1.5;
}

.panel-hea .buClose {
  box-sizing: content-box;
  width: 1em;
  height: 1em;
  font-size: 2rem;
  font-weight: bold;
  font-family: Mojangles, monospace;
  color: #700000;
  opacity: 1;
  background-color: #00000000;
  padding: .25em;
  margin: 0 0 0 auto;
}

.panel-bod {
  position: relative;
  padding: 1rem;
}

.panel-bod p {
  margin: 0;
}

.panel-foo {
  display: flex;
  padding: .5rem;
  border-top: 1px solid #dee2e6;
}