body {
  margin: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  font-family: "Times New Roman", sans-serif;
  margin: 0;
  padding: 0;
  min-height: 100vh;
  color: white;
  background-color: black;
  background-image: url(/public/assets/imgs/map.webp);
  background-attachment: fixed;
  background-position: center;
  background-size: cover;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

button {
  padding: 12px 25px;
  font-family: "Times New Roman", sans-serif;
  font-size: medium;
  background-color: #4caf50;
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 5px;
  transition: transform 0.3s ease, background-color 0.3s ease;
  display: block;
  margin: 0 auto;
  width: fit-content;
}

button:hover {
  transform: scale(1.1);
  background-color: #218838;
}

.main-conta {
  margin-top: 80px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.content {
  display: flex;
  /* margin-top: 40px; */
  height: 100%;
  max-width: 100vw;
  /* flex-direction: column; */
  justify-content: center;
}
.bg-hidden {
  display: none;
}
.paused {
  animation-play-state: paused !important;
}
#bgVideo {
  transform: translateX(-50%) translateY(-50%);
  filter: blur(10px);
  position: fixed;
  top: 50%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: 100%;
  z-index: -1;
  overflow: hidden;
  -o-object-fit: cover;
  object-fit: cover;
}
@font-face {
  font-family: "Mojangles";
  src: url("/public/fonts/Mojangles.ttf") format("ttf");
  font-weight: normal;
  font-style: normal;
}
