.bg {
  margin-top: -80px;
  background-color: #016425;
  background-image: url(/public/assets/imgs/bg-olds.webp);
  filter: blur(10px);
  background-attachment: fixed;
  background-position: center;
  background-size: cover;
  height: 100%;
  width: 100%;
  position: fixed;
  z-index: -1;
}

.img-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
  max-width: 100%;
  margin-bottom: 20px;
}

