.img-item-wrapper {
  max-width: 455px; /* Giữ kích thước wrapper theo yêu cầu */
  max-height: 256px; /* Giữ kích thước wrapper theo yêu cầu */
  overflow: hidden; /* Ẩn phần ảnh bị tràn */
  cursor: pointer;
  position: relative;
}
.img-item {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.2s ease-in-out;
}
.img-item-wrapper:hover .img-item {
  transform: scale(1.1);
}
.img-item-wrapper:hover {
  outline: 2px dashed #ccc;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  max-width: 90%;
  max-height: 90%;
  /* overflow: auto; */
}

.modal-img {
  max-width: 100%;
  max-height: 80vh;
  object-fit: contain;
}

.modal-caption {
  text-align: center;
  margin-top: 10px;
  margin-bottom: 0;
  font-family: Mojangles, monospace;
  color: black;
}
